<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:01:17
-->
<template>
  <div class="edit-modal-three">
    <modal
      width="58.65vw"
      height="35.31vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit.png"
        alt=""
      />

      <a-form-model 
        layout="inline" 
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="号牌种类：" prop="plateType">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择号牌种类"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              蓝底白字
            </a-select-option>
            <a-select-option value="2">
              绿底白字
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="号牌号码：" prop="plateNum">
          <a-input
            class="default-input"
            placeholder="请输入号牌号码"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="车牌颜色：">
          <a-input
            class="default-input"
            placeholder="请输入车牌颜色"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="车辆颜色：">
          <a-input
            class="default-input"
            placeholder="请输入车辆颜色"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="车主姓名：" prop="carOwnerName">
          <a-input
            class="default-input"
            placeholder="请输入车主姓名"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="性别：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择性别"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              男
            </a-select-option>
            <a-select-option value="2">
              女
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="证件号码：" prop="identity">
          <a-input
            class="default-input"
            placeholder="请输入证件号码"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="等级：" prop="grade">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择等级"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              等级1
            </a-select-option>
            <a-select-option value="2">
              等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="类别：" prop="type">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择类别"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              类别1
            </a-select-option>
            <a-select-option value="2">
              类别2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="开始时间：" prop="startTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="结束时间：">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            placeholder="请进行拦截"
            :disabled="isShow"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')">取消</a-button>
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        plateType: "",
        plateNum: "",
        carOwnerName: "",
        identity: "",
        grade: "",
        type: "",
        startTime: ""
      },
      rules: {
        plateType: [
          {
            required: true,
            message: "请号牌种类！",
            trigger: "blur"
          }
        ],
        plateNum: [
          {
            required: true,
            message: "请输入号牌号码！",
            trigger: "blur"
          }
        ],
        carOwnerName: [
          {
            required: true,
            message: "请输入车主姓名！",
            trigger: "blur"
          }
        ],
        identity: [
          {
            required: true,
            message: "请输入身份证号！",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            message: "请选择等级！",
            trigger: "blur"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择类别！",
            trigger: "blur"
          }
        ],
        startTime: [
          {
            required: true,
            message: "请选择开始时间！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == '查看详情'
    }
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
